.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDeskTop {
      margin-bottom: 104px;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;
    margin-bottom: 28px;
    transition: transform 0.3s;

    @include onDeskTop {
      margin-bottom: 32px;
    }

    &:hover {
      transform: scale(1.2);
    }
  }

  &__photo {
    grid-column: 2/4;
    width: 100%;
    border-radius: 50%;
  }

  &__name {
    margin-bottom: 4px;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $colorBlue;
  }

  &__role {
    margin-bottom: 12px;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }
}

.link {
  color: $colorBlue;
  text-decoration: none;
}
